import React from 'react'
import { uniqueId } from 'lodash'
import * as PropTypes from 'prop-types'
import { DialogContainer } from 'react-md'

export const modalTypes = {
  info: 'info',
  confirm: 'confirm',
}

const Modal = ({ variant, title, content, submitModal, closeModal }) => {
  let actions = null

  switch (variant) {
    case modalTypes.confirm:
      actions = [
        {
          id: uniqueId('dialog-cancel_'),
          children: 'Cancel',
          onClick: closeModal,
        },
        {
          id: uniqueId('dialog-confirm_'),
          children: 'Yes, I agree',
          onClick: submitModal,
        },
      ]
      break

    case variant === modalTypes.info:
    default:
      actions = [
        {
          id: uniqueId('dialog-confirm_'),
          children: 'Ok',
          onClick: submitModal,
        },
      ]
  }

  /* closeOnEsc={false}*/
  /*fullPage={true}*/
  /*renderNode={document.body}*/

  return (
    <DialogContainer
      id={uniqueId('modal_')}
      title={title}
      actions={actions}
      visible={true}
      defaultVisibleTransitionable={true}
      disableScrollLocking={true}
    >
      {content}
    </DialogContainer>
  )
}

Modal.defaultProps = {
  submitModal: null,
  closeModal: null,
}

Modal.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  variant: PropTypes.string.isRequired,
  submitModal: PropTypes.func,
  closeModal: PropTypes.func,
}

export default Modal
